body {
  background-color: #eee9e9;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.title {
  text-align: center;
}

.calculator {
  background-color: #302f2f;
  border-radius: 10px;
  width: 320px;
  padding: 1rem;
  overflow: hidden;
  box-shadow: 0 0 10px #0000001a;
}

.calcName {
  text-align: left;
  color: #fff;
}

.displayScreen {
  color: #fff;
  box-sizing: border-box;
  background: linear-gradient(#4a5c2e 0%, #b4e761 100%);
  border-bottom: 1rem solid #302f1f;
  border-radius: 2rem;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  height: 8rem;
  padding: 4rem;
  display: flex;
}

.previousNumber {
  color: #faf4f4;
  font-size: 24px;
}

.display {
  margin: 0;
  font-size: 36px;
}

.buttons {
  grid-gap: 1px;
  background-color: #ccc;
  border-radius: 1rem;
  grid-template-columns: repeat(4, 1fr);
  gap: .25rem;
  padding: 10px;
  display: grid;
}

.button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 12rem;
  padding: 20px;
  font-size: 24px;
  transition: background-color .3s;
}

.disabled {
  background-color: #3d3c3c;
  border: none;
  border-radius: 12rem;
  padding: 20px;
  font-size: 24px;
}

.button:hover {
  background-color: #f0f0f0;
}

.button:active {
  background-color: #e0e0e0;
}

.operations {
  color: #fff;
  background-color: #f9a825;
}

.operations:hover {
  background-color: #fbc02d;
}

.operations:active {
  background-color: #f57f17;
}

.clear {
  color: #fff;
  background-color: #e53935;
}

.clear:hover {
  background-color: #ef5350;
}

.clear:active {
  background-color: #c62828;
}

.backspace {
  color: #fff;
  background-color: #ff7043;
}

.backspace:hover {
  background-color: #ff8a65;
}

.backspace:active {
  background-color: #d84315;
}

.equals {
  color: #fff;
  background-color: #43a047;
  grid-column: span 2;
}

.equals:hover {
  background-color: #66bb6a;
}

.equals:active {
  background-color: #2e7d32;
}
